import './App.css';
import React, { useEffect, useState } from 'react'
import axios from 'axios'

const App = () => {

  const [name, setName] = useState('')
  const [rating, setRating] = useState()
  const [movielist,setMovielist] = useState([])
  const [counter,setCounter] = useState(0)


  const hook=()=>{
    axios.get('http://localhost:3001/data').then(res=>{
      setMovielist(res.data)
      console.log(res.data);
    })
  }
  useEffect(hook,[counter])


  const Rate = () => {
    const rateObj = {
      name: name,
      rating: rating
    }
    
    axios.post('http://localhost:3001/add', rateObj).then(res => {
      setMovielist(movielist.concat(res.data))
      setCounter(counter+1)
    })
    setName('')
    setRating('')
  }

  const Delete=({name,id})=>{
    let mname=name
    let mid=id
    
    axios.delete(`http://localhost:3001/delete/${mid}`,{data:{mname}}).then(res=>{
      movielist.concat(res.data)
      setCounter(counter+1)
    })
  }



  return (
    <div className="App">
      <h1>Movie Catalogue</h1>
      <input type="text" value={name} placeholder="Enter movie name" onChange={(event) => {
        setName(event.target.value)
      }} />
      <input type="number" value={rating} placeholder="Enter rating" onChange={(event) => {
        setRating(event.target.value)
      }} />
      <button className="addbtn" onClick={Rate}>Add</button>
      <h2>Movies</h2>
      {movielist.map((m,id)=>{
        return(
        <div className="movieList" key={id}><span className="movies">{m.name}</span> : <span className="rating">{m.rating}</span>
          <DeleteButton name={m.name} rating={m.rating} DeleteData={Delete} id={m.id}/>
          </div>)
      })}
    </div>
      
  );
}

const DeleteButton=({name,rating,id,DeleteData})=>{
  
  return(
    <button className="deleteBtn" onClick={()=>{DeleteData({name,id})}}>delete</button>
  )

}

export default App;
